@use  './base';
@use './colors' as *;
@use './fonts' as *;

.btn{
    border-radius: 32px;

    &-primary{
        background: $Primary-clr;
        color: $White-600;
        font-weight: $font-Weight-600;
        box-shadow: $Box-Shadow-1;
        padding: 0.8em 4.4em;
        text-decoration: none;
        display: block;
        height: max-content;
        width: max-content;
    }
}