@use  './base';
@use './colors' as *;
@use './fonts' as *;
@use './utlis' as *;
@use './btn';

header{
    height: 10vh;
    padding: $container-padding;
}

nav{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;

    ul{
        display: flex;
        list-style: none;
    }

    li{
        margin-right: 20px;
    }

    a{
        color: $Black-600;
        text-decoration: none;
    }
}

.hero{
    height: 80vh;
    width: 100%;
    display: grid;
    align-items: center;
    padding: 0px $container-padding-left 0px;

    .row{
        display: flex;
        align-items: center;

        .left{
            width: 50%;

            .title{
                font-weight: $font-Weight-500;
                font-size: 3em;
                width: 300px;
                line-height: 1.4em;
                margin: 0;
                margin-bottom: 10px;
            }

            .subtext{
                margin: 0;
                margin-bottom: 20px;
                width: 400px;
            }

            .text-gradient{
                background: $Gradient-1;
                -webkit-background-clip:text;
                color: transparent;
                font-weight: $font-Weight-700;
            }
        }

        .right{
            padding-left: 5em;

            .hero-img{
                width: 1000px;
            }

            .bg{
                position: absolute;
                z-index: -1;
                right: 0;
                top: 10em;
                width: 710px;
            }
        }

    }
    .box{
        position: absolute;
        top: 3%;
        bottom: 20%;
        right: 7.4em;
        height: 92vh;
        width: 35vw;
        z-index: -2;
        background: $Secondary-clr;
        border-radius: 19px;
    }
}
